<template>
  <div :class="$root.themeHomeChange === '1' ? 'complianceTasks_light' : 'complianceTasks'">
    
    <div class="header">
      <div class="left">
        <img src="@/assets/img/companyCenter/jiantou.png" />
        <span class="headerTitle">{{title}}</span> 
      </div>
      <!-- <el-radio-group
        v-model="timeCheck"
        @change="radioChange">
        <el-radio
          v-for="(item, index) in radioList"
          :label="item.value"
          :key="index"
        >{{item.label}}</el-radio>
      </el-radio-group> -->
      <el-date-picker
        v-model="date"
        size="mini"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        value-format="yyyy-MM-dd"
        style="width: 240px;"
      >
      </el-date-picker>
    </div>
    
    <div class="content">
      <div id="lineCharts" ref="lineCharts"></div>
    </div>
  </div>
</template>

<script>
  let option = {
    tooltip: {
      trigger: 'axis',
      backgroundColor: 'rgba(42, 92, 176, .6)',
      borderColor: "rgba(0, 255, 239, 1.0)",
      textStyle: {
        color: '#00FFEF'
      },
      axisPointer: {
        type: 'cross',
      }
    },
    // toolbox: {
    //   right: '20px',
    //   feature: {
    //     dataView: { show: true, readOnly: true, title: '图表数据' },
    //     // magicType: { show: true, type: ['line', 'bar'] },
    //     // restore: { show: true },
    //     saveAsImage: { show: true, title: '保存为图片'}
    //   }
    // },
    grid: {
      top: '20%',
      bottom: '10%',
      left: '5%',
      right: '5%',
    },
    legend: [
      {
        data: [
          {
            name: '当前问题数'
          }
        ],
        textStyle: {
          color: "rgba(198, 198, 198, 1.0)"
        },
        itemWidth: 15, //矩形宽度
        itemHeight: 15, //矩形高度
        top: '10px',
        left: '40%',
      },
      {
        data: [
          {
            name: '轻微违规整改率',
            icon: 'roundRect', // 用矩形替换
            itemStyle: {
              shadowColor: "#f5fd5c",
              shadowOffsetX: 0,
              shadowOffsetY: 0,
              opacity: 1,
              shadowBlur: 10,
              type: "solid",
              width: 3
            }
          }
        ],
        textStyle: {
          color: "rgba(198, 198, 198, 1.0)"
        },
        itemWidth: 15, //矩形宽度
        itemHeight: 3, //矩形高度
        top: '10px',
        left: '50%',
      }
    ],
    xAxis: [
      {
        type: 'category',
        data: ['办公室', '合规与监审部', '系统规划部', '资产管理部', '市场营销部', '安全监管部', '人力资源部', '党建工作部', '创新与数字化部', '财务部'],
        axisPointer: {
          type: 'shadow'
        },
        axisLabel: {
          color: '#fff',
          fontWeight: 'bold',
          interval:0,//使x轴横坐标全部显示
        }
      }
    ],
    yAxis: [
      {
        type: 'value',
        name: '单位：个',
        min: 0,
        max: 100,
        interval: 20,
        splitLine: {
          lineStyle: {
            color: "rgba(0, 102, 185, .8)"
          }
        },
        axisLabel: {
          color: '#0094d3',
          formatter: '{value}'
        },
        nameTextStyle: {
          color: "rgba(66, 134, 206, 1.0)"
        }
      },
      {
        type: 'value',
        min: 0,
        max: 100,
        interval: 20,
        splitLine: {
          lineStyle: {
            color: "rgba(0, 102, 185, .8)"
          }
        },
        axisLabel: {
          color: '#0094d3',
          formatter: '{value}'
        },
        nameTextStyle: {
          color: "rgba(66, 134, 206, 1.0)"
        }
      }
    ],
    series: [
      {
        name: '当前问题数',
        type: 'bar',
        barWidth: "20%",
        itemStyle: {
          normal: {
            label: {
              // formatter: "{b}"+"人",
              show: true,
              position: "top",
              textStyle: {
                fontWeight: "bolder",
                fontSize: "12",
                color: '#00FFEF'
              }
            },
            barBorderRadius: [2, 2, 0, 0],
            color: new echarts.graphic.LinearGradient(
              0, 0, 0, 1,
              [
                {offset: 0, color: '#00FFEF'},
                {offset: 1, color: '#007EFF'}
              ]
            )
          }
        },
        data:[68,66,65,63,69,67,62,60,64,70],
      },
      {
        name: '轻微违规整改率',
        type: 'line',
        showSymbol: true,
        // symbol: 'circle',     //折点设定为实心点
        symbolSize: 12,   //设定实心点的大小
        itemStyle: {
            color: '#fdea73',
            shadowColor: '#fdea73',
            shadowBlur: 20,
        },
        lineStyle: {
          shadowColor: "#fdea73",
          shadowOffsetX: 0,
          shadowOffsetY: 0,
          opacity: 1,
          shadowBlur: 8,
          type: "solid",
          width: 3
        },
        yAxisIndex: 1,
        data:  [88,91,90,87,88,89,85,88,91,90],
      }
    ]
  };
  import * as echarts from "echarts";
  import bus from '@/utils/bus';
  export default {
    name: "complianceTasks",
    props: {
      title: {
        type: String,
        default: () => {
          return '智能组件';
        },
      },
      data: {
        type: Array,
        default: () => {
          return [];
        },
      }
    },
    watch: {
      '$root.themeHomeChange'() {
        this.init();
      }
    },
    data(){
      return {
        time: null,
        // timeCheck: '1',
        date: '',
        radioList: [
          {
            label: '月',
            value: '0'
          },
          {
            label: '季度',
            value: '1'
          },
          {
            label: '半年',
            value: '2'
          },
          {
            label: '全年',
            value: '3'
          }
        ],
        mychart: ''
      }
    },
    mounted() {
      this.time = setInterval(()=>{
        this.mychart.resize()
      },300);
      this.init();
    },
    created() {},
    methods: {
      setTheme(){
        // window.console.log(23123);
        //样式区分
        if (this.$root.themeHomeChange === '1'){  //浅色背景
          option.legend.forEach(item => {
            item.textStyle = {color: '#333'}
          })
          // option.legend.textStyle = {color: '#333333'}
          option.xAxis[0].axisLabel.textStyle = {
            color: "#333333",
            margin: 15,
            fontWeight: 'bold',
          }
          option.yAxis[0].axisLabel.textStyle = {
            color: "#333333",
            margin: 15,
            fontWeight: 'bold',
          }
          option.series[1].itemStyle.color = '#FFB948';
          option.series[1].itemStyle.shadowColor = '#FFB948';
          option.series[1].lineStyle.shadowColor = '#FFB948';
          
          option.yAxis.forEach(item => {
            item.splitLine = {//网格线样式
              show: true,
              lineStyle:{
                color: '#E6E6E6',
                width: 1,
                type: 'solid'
              }
            }
          });
        }else if(this.$root.themeHomeChange==='2'){ //深色背景
          option.legend.forEach(item => {
            item.textStyle = {color: '#ffffff'}
          })
          // option.legend.textStyle = {color: '#ffffff'};
          option.xAxis[0].axisLabel.textStyle = {
            color: "#ffffff",
            margin: 15,
            fontWeight: 'bold',
          }
          option.yAxis[0].axisLabel.textStyle = {
            color: "#66B9FF",
            margin: 15,
            fontWeight: 'bold',
          }
          option.series[1].itemStyle.color = '#fdea73';
          option.series[1].itemStyle.shadowColor = '#fdea73';
          option.series[1].lineStyle.shadowColor = '#fdea73';
          option.yAxis.forEach(item => {
            item.splitLine = {//网格线样式
              show: true,
              lineStyle:{
                color: '#66B9FF',
                width: 1,
                type: 'solid'
              }
            }
          });
        }
      },
      init() {
        // 初始化时间选择框数据
        let nowDate = new Date();
        let year = nowDate.getFullYear(); 
        let month = nowDate.getMonth() + 1;
        let day = nowDate.getDate();
        month = (month < 10 ? "0" + month : month);
        
        let yearFirstDay = nowDate.getFullYear() + '-01-01'
        
        //样式
        this.setTheme();
        
        this.date = [yearFirstDay, year + '-' + month + '-' + day];
        
        this.mychart = echarts.init(this.$refs.lineCharts);
        
        this.mychart.setOption(option);
        
        window.addEventListener("resize", ()=>{
          this.mychart.resize()
        });
      },
      // 单选切换
      radioChange(key) {
        // let max = parseInt(Math.random() * 200, 10)+1;
        // if(key == 'year') {
        //   (this.title === '智能监测' ? this.monitorList : this.gradingList).forEach(item => {
        //     item.total = parseInt(max * Math.ceil(Math.random()*10) * 12 / Math.ceil(Math.random()*10));
        //   })
        // } else if(key == 'quarter') {
        //   (this.title === '智能监测' ? this.monitorList : this.gradingList).forEach(item => {
        //     item.total = parseInt(max * Math.ceil(Math.random()*10) * 4 / Math.ceil(Math.random()*10));
        //   })
        // } else {
        //   (this.title === '智能监测' ? this.monitorList : this.gradingList).forEach(item => {
        //     item.total = parseInt(max * Math.ceil(Math.random()*10) * 1 / Math.ceil(Math.random()*10));
        //   })
        // }
        window.console.log(key);
      }
    },
    beforeDestroy() {
      bus.$off('isCollapse');
      clearInterval(this.time);
      window.removeEventListener("resize", () => {
        this.mychart.resize();
      });
    }
  }
</script>

<style scoped lang="scss">
  .complianceTasks{
    width: calc(100% - 20px);
    display: flex;
    flex-wrap: wrap;
    height: 390px;
    padding: 10px;
    margin-top: 15px;
    background: url(../../assets/img/riskControlCenter/jhxhgrw.png) no-repeat;
    background-size: 100% 100%;
    /deep/ .el-range-editor--mini.el-input__inner {
      background-color: #041338;
      border: 1px solid #509be1;
      border-radius: 5px;
      .el-range-input, .el-range-separator {
        background-color: #041338;
        color: #FFF;
      }
      
      input::-webkit-input-placeholder {
        color: #fff;
      }
       
      input::-moz-placeholder {
        /* Mozilla Firefox 19+ */
        color: #fff;
      }
     
      input:-moz-placeholder {
        /* Mozilla Firefox 4 to 18 */
        color: #fff;
      }
     
      input:-ms-input-placeholder {
        /* Internet Explorer 10-11 */
        color: #fff;
      }
    }
    .header {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-left: 5px;
      height: 32px;
      .left {
        display: flex;
        align-items: center;
        // padding-left: 10px;
        .headerTitle {
          font-family: MicrosoftYaHei-Bold;
          font-size: 17px;
          color: #00F8FF;
          font-weight: 600;
          margin-left: 5px;
        }
      }
    }
    .content {
      width: 100%;
      height: calc(100% - 42px);
      display: flex;
      div {
        width: 100%;
        height: 100%;
        position: relative;
      }
    }
  }
  
  .complianceTasks_light {
    width: calc(100% - 20px);
    display: flex;
    flex-wrap: wrap;
    height: 390px;
    padding: 10px;
    margin-top: 15px;
    // background: url(../../assets/img/riskControlCenter/jhxhgrw.png) no-repeat;
    background: #fff;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    border-radius: 6px;
    background-size: 100% 100%;
    position: relative;
    overflow: hidden;
    &::after {
      content: "";   
      display: block;
      width: 50px;   
      height: 5px;
      background: #4085fa;
      position: absolute;
      top: -5px;
      left: -15px;
      transform: rotate(-45deg);
      // border-radius: 50px 0;
    }
    &::before {
      content: "";   
      display: block;
      width: 50px;   
      height: 5px;
      background: #4085fa;
      position: absolute;
      bottom: -5px;
      right: -15px;
      transform: rotate(-45deg);
      // border-radius: 50px 0;
    }
    // 组件样式覆盖
    /deep/ .el-radio__input.is-checked+.el-radio__label {
      color: #4389F9!important;
    }
    /deep/ .el-radio-group {
      .el-radio:not(is-checked) {
        color: #333;
      }
      .el-radio__inner {
        background-color: #fff!important;
        border: 1px solid #509be1;
        &::after {
          width: 7px;
          height: 7px;
          // border: 2px solid #03366f;
          background-color: #4389F9;
        }
      }
    }
    /deep/ .el-range-editor--mini.el-input__inner {
      background-color: #fff;
      border: 1px solid #d2d2d2;
      border-radius: 5px;
      .el-range-input, .el-range-separator {
        background-color: #fff;
        color: #333;
      }
      
      input::-webkit-input-placeholder {
        color: #333;
      }
       
      input::-moz-placeholder {
        /* Mozilla Firefox 19+ */
        color: #333;
      }
     
      input:-moz-placeholder {
        /* Mozilla Firefox 4 to 18 */
        color: #333;
      }
     
      input:-ms-input-placeholder {
        /* Internet Explorer 10-11 */
        color: #333;
      }
    }
    .header {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-left: 5px;
      height: 32px;
      .left {
        display: flex;
        align-items: center;
        // padding-left: 10px;
        .headerTitle {
          font-family: MicrosoftYaHei-Bold;
          font-size: 17px;
          color: #4389F9;
          font-weight: 600;
          margin-left: 5px;
        }
      }
    }
    .content {
      width: 100%;
      height: calc(100% - 42px);
      display: flex;
      div {
        width: 100%;
        height: 100%;
        position: relative;
      }
    }
  }
</style>